<template>
    <div class="rkmd">
        <Home></Home>
        <div class="map-container">
            <div class="map"></div>
            <div class="top-left"><img src="@/assets/enter-border-1.jpg" alt="" width="350px"></div>
            <div class="bottom-left"><img src="@/assets/enter-border-1.jpg" alt="" width="350px"></div>
            <div class="top-right"><img src="@/assets/enter-border-2.jpg" alt="" width="350px"></div>
            <div class="bottom-right"><img src="@/assets/enter-border-2.jpg" alt="" width="350px"></div>
        </div>
        <div class="currentTime">
            <img src="@/assets/enter-border-1.jpg" alt="" width="150px">
            <img src="@/assets/enter-border-2.jpg" alt="" width="150px">
            <div class="time">
                <svg style="display:none" class="animated fadeIn">
                    <text id="time" text-anchor="middle" x="50%" y="0%" class="words words-1"></text>
                    <text id="time" text-anchor="middle" x="50%" y="0%" class="words words-2"></text>
                    <text id="time" text-anchor="middle" x="50%" y="0%" class="words words-3"></text>
                    <text id="time" text-anchor="middle" x="50%" y="0%" class="words words-4"></text>
                </svg>
            </div>
        </div>
    </div>
</template>

<script>
    import Home from '../components/home';
    export default {
        components: {
            Home
        },
        beforeDestroy() {
            if (this._timeOut) {
                clearInterval(this._timeOut);
            }
        },
        methods: {
            showtime() {
                var noatime = new Date();
                var h = noatime.getHours(),
                    m = noatime.getMinutes(),
                    s = noatime.getSeconds();
                h = this.checktime(h);
                m = this.checktime(m);
                s = this.checktime(s);
                return h + ":" + m + ":" + s;
            },
            checktime(x) {
                if (x < 10) {
                    x = "0" + x;
                }
                return x;
            }
        },
        mounted() {
            setTimeout(() => {
                $('svg').css('display','block');
                  var h = $('.currentTime').height()/2 + $('svg text')[0].getBoundingClientRect().height/3;
                //   var h2 =  ;
                  console.log(h)
                  $('svg text').css('transform','translateY('+h+'px)');
            }, 1200);
      
            var that = this;
            setInterval(function() {
                $('text').each(function() {
                    $(this).html(that.showtime());
                })
            }, 1000);
            var myChart = echarts.init(document.querySelector('.map'));
            new Promise(function(suc, rej) {
                console.log(1)
                var time = new Date();
                var h = time.getHours(),
                    m = time.getMinutes(),
                    s = time.getSeconds();
                var nowtime = String(h) + String(m) + String(s);
                $.get('http://121.199.31.199:8081/itationtude?timestamp=' + nowtime, function(res) {
                    // 处理接口数据
                    geoCoordMap = {};
                    data.length = 0;
                    for (var prop in res) {
                        geoCoordMap[prop] = res[prop];
                        data.push({
                            name: prop,
                            value: 1000
                        })
                    }
                    option.series[0].data = convertData(data);
                    myChart.setOption(option);
                    myChart.hideLoading();
                    suc();
                });
            }).then(function() {
                that._timeOut = setInterval(() => {
                    var time = new Date();
                    var h = time.getHours(),
                        m = time.getMinutes(),
                        s = time.getSeconds();
                    var nowtime = String(h) + String(m) + String(s);
                    $.get('http://121.199.31.199:8081/itationtude?timestamp=' + nowtime, function(res) {
                        // 处理接口数据
                        geoCoordMap = {};
                        data.length = 0;
                        for (var prop in res) {
                            geoCoordMap[prop] = res[prop];
                            data.push({
                                name: prop,
                                value: 1000
                            })
                        }
                        option.series[0].data = convertData(data);
                        myChart.setOption(option);
                        myChart.hideLoading();
                    });
                }, 10000);
            })
            var geoCoordMap = {}
            var data = []
            var convertData = function(data) {
                var res = [];
                for (var i = 0; i < data.length; i++) {
                    var geoCoord = geoCoordMap[data[i].name];
                    if (geoCoord) {
                        res.push({
                            name: data[i].name,
                            value: geoCoord.concat(data[i].value)
                        });
                    }
                }
                return res;
            };

            var convertedData = [
                convertData(data),
                convertData(data.sort(function(a, b) {
                    return b.value - a.value;
                }).slice(0, 6))
            ];

            myChart.showLoading({
                animation: 'QuarticIn',
                text: 'Loading',
            });

            var option = {
                tooltip: {
                    trigger: 'item'
                },

                grid: {
                    right: '2%',
                    top: '20%',
                    bottom: '15%',
                    width: '15%'
                },

                xAxis: {
                    type: 'value',
                    scale: true,
                    position: 'top',
                    boundaryGap: false,
                    splitNumber: 4,
                    splitLine: {
                        show: false
                    },
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        margin: 2,
                        textStyle: {
                            color: '#aaa'
                        }
                    },
                },

                yAxis: {
                    type: 'category',
                    nameGap: 95,
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        interval: 0,
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    data: []
                },

                visualMap: {
                    min: 0,
                    max: 1000,
                    left: 50,
                    bottom: 50,
                    show: false,
                    inRange: {
                        color: ['blue', 'blue', 'green', 'yellow', 'red']
                    },
                    textStyle: {
                        color: '#fff'
                    }
                },
                bmap: {
                    center: [123.41, 41.81],
                    zoom: 14,
                    roam: true,
                    mapStyle: {
                        'styleJson': [{
                            'featureType': 'water',
                            'elementType': 'all',
                            'stylers': {
                                'color': '#031628'
                            }
                        }, {
                            'featureType': 'land',
                            'elementType': 'geometry',
                            'stylers': {
                                'color': '#000102'
                            }
                        }, {
                            'featureType': 'highway',
                            'elementType': 'all',
                            'stylers': {
                                'visibility': 'off'
                            }
                        }, {
                            'featureType': 'arterial',
                            'elementType': 'geometry.fill',
                            'stylers': {
                                'color': '#000000'
                            }
                        }, {
                            'featureType': 'arterial',
                            'elementType': 'geometry.stroke',
                            'stylers': {
                                'color': '#0b3d51'
                            }
                        }, {
                            'featureType': 'local',
                            'elementType': 'geometry',
                            'stylers': {
                                'color': '#000000'
                            }
                        }, {
                            'featureType': 'railway',
                            'elementType': 'geometry.fill',
                            'stylers': {
                                'color': '#000000'
                            }
                        }, {
                            'featureType': 'railway',
                            'elementType': 'geometry.stroke',
                            'stylers': {
                                'color': '#08304b'
                            }
                        }, {
                            'featureType': 'subway',
                            'elementType': 'geometry',
                            'stylers': {
                                // 'color': '#000000',// 默认透明显示地铁线路
                                'lightness': -70
                            }
                        }, {
                            'featureType': 'building',
                            'elementType': 'geometry.fill',
                            'stylers': {
                                'color': '#000000'
                            }
                        }, {
                            'featureType': 'all',
                            'elementType': 'labels.text.fill',
                            'stylers': {
                                'color': '#857f7f'
                            }
                        }, {
                            'featureType': 'all',
                            'elementType': 'labels.text.stroke',
                            'stylers': {
                                'color': '#000000'
                            }
                        }, {
                            'featureType': 'building',
                            'elementType': 'geometry',
                            'stylers': {
                                'color': '#022338'
                            }
                        }, {
                            'featureType': 'green',
                            'elementType': 'geometry',
                            'stylers': {
                                'color': '#062032'
                            }
                        }, {
                            'featureType': 'boundary',
                            'elementType': 'all',
                            'stylers': {
                                'color': '#465b6c'
                            }
                        }, {
                            'featureType': 'manmade',
                            'elementType': 'all',
                            'stylers': {
                                'color': '#022338'
                            }
                        }, {
                            'featureType': 'label',
                            'elementType': 'all',
                            'stylers': {
                                'visibility': 'off'
                            }
                        }]
                    }
                },
                series: [{
                    name: '热图',
                    type: 'heatmap',
                    coordinateSystem: 'bmap',
                    pointSize: 20,
                    // blurSize: 20,
                    data: convertData(data)
                }]
            };
        }
    }
</script>

<style scoped>
    .rkmd {
        position: fixed;
        width: 100%;
        height: 100%;
        background: #051b4a;
        background: url('../assets/bg.png');
        background-size: 100% 100%;
    }
    
    .map-container {
        position: fixed;
        width: 60%;
        left: 19.5%;
        height: 76%;
        top: 12%;
        z-index: -1;
        border-radius: 1px;
        border: 2px solid rgb(59, 73, 134);
    }
    
    .map-container img {
        vertical-align: middle;
        pointer-events: none;
    }
    
    .map {
        position: absolute;
        width: 100%;
        height: 100%;
    }
    
    .currentTime {
        position: fixed;
        width: 25%;
        right: 0;
        height: 20%;
        top: 40%;
        background: url('../assets/data2.png');
        background-size: 100% 100%;
    }
    
    .currentTime>img:nth-of-type(1) {
        position: absolute;
        bottom: 0;
        left: 0;
    }
    
    .currentTime>img:nth-of-type(2) {
        position: absolute;
        top: 0;
        left: 0;
        transform: rotateY(180deg)
    }
    .time{
        height: 100%;
    }
    .time p {
        margin: 5px;
        color: white;
        font-size: 18px;
    }
    
    .top-left {
        position: absolute;
        left: -3px;
        top: -7px;
        transform: rotateX(180deg);
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .top-right {
        position: absolute;
        right: -3px;
        top: -7px;
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .bottom-right {
        position: absolute;
        right: -3px;
        bottom: -7px;
        transform: rotateX(180deg);
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .bottom-left {
        position: absolute;
        left: -3px;
        bottom: -7px;
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    /* 时间特效文字 */
    
    svg {
        width: 100%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        height: 100%;
    }
    .words {
        font-size: 80px;
        /* font-weight: bold; */
        text-transform: uppercase;
        fill: none;
        stroke-width: 3px;
        stroke-dasharray: 90, 310;
        animation-name: drawing;
        animation-duration: 8s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
    }
    
    .words-1 {
        stroke: deepskyblue;
        text-shadow: 0 0 50px deepskyblue;
        animation-delay: -2s;
    }
    
    .words-2 {
        stroke: lightseagreen;
        text-shadow: 0 0 50px lightseagreen;
        animation-delay: -4s;
    }
    
    .words-3 {
        stroke: orange;
        text-shadow: 0 0 50px orange;
        animation-delay: -6s;
    }
    
    .words-4 {
        stroke: purple;
        text-shadow: 0 0 50px purple;
        animation-delay: -8s;
    }
    
    @keyframes drawing {
        100% {
            stroke-dashoffset: -400;
        }
    }
</style>


