<template>
    <div class="rkcx">
        <Home></Home>
        <div class="chart-container">
            <div id="chart"></div>
            <div class="top-left"><img src="@/assets/enter-border-1.jpg" alt="" width="350px"></div>
            <div class="bottom-left"><img src="@/assets/enter-border-1.jpg" alt="" width="350px"></div>
            <div class="top-right"><img src="@/assets/enter-border-2.jpg" alt="" width="350px"></div>
            <div class="bottom-right"><img src="@/assets/enter-border-2.jpg" alt="" width="350px"></div>
            <div class="info"><p>全部人口</p></div>
        </div>
        <div class="data">
          <button class="data-title">人口出行分析</button>
          <div class="datalist-title">
                <p style="color:#de9510">时间</p>
                <p style="color:#4397ff">速度(m/s)</p>
                <p style="color:rgb(83, 196, 199)">状态</p>
          </div>
          <div class="data-content">
                <el-collapse v-model="activeName"  accordion>
                    <el-collapse-item :title="'id：'+item.id" v-for="item in res" :key="item.id">
                        <div class="datalist"  @click="showLine(item)"><p style="color:#de9510;font-weight:1000;font-size:15px;cursor:pointer">展示出行路线图</p></div>
                        <hr style="margin:0;padding:0">
                        <div class="datalist"  @click="showRecommend(item)"><p style="color:#4397ff;font-weight:1000;font-size:14px;cursor:pointer">获取当前时间出行方式推荐</p></div>
                        <hr style="margin:0;padding:0">
                        <div class="datalist" v-for="(itemChild,index) in item.time" :key="index">
                          <p class="time-p">{{item.time[index]}}</p>
                          <p style="transform:translateY(-50%);margin-top:-10px" :class="'timeline p'+index">{{item.speed[index]}}</p>
                          <p style="font-weight:1000;font-size:13.5px;color:turquoise" v-if="item.state[index]=='驻留'" :class="'p'+index">{{item.state[index]}}</p>
                          <p style="font-weight:1000;font-size:13.5px" :class="'p'+index" v-else>{{item.state[index]}}</p>
                        </div>
                    </el-collapse-item>
                </el-collapse>
          </div>
        </div>
        <div class="average" style="display: none;">
            <div class="back el-icon-close animated fadeIn" @click="closeAver"></div>
            <div class="average-chart"></div>
        </div>
        <div class="btn">
          <button class="yx">获取夜行信息</button>
          <button class="all">获取全部出行信息</button>
      </div>
    </div>
</template>

<script>
    import Home from '../components/home';
    export default {
        components: {
            Home
        },
        methods: {
            closeAver() {
                $('.average').css('display', 'none');
            },
            getChart(data) {
                console.log(data)

                function angleText(i, num) {
                    //每个元素的角度
                    var everyAngle = 360 / num;
                    //文字现在所在的角度
                    var currentAngle = i * everyAngle + everyAngle / 2;
                    //文字所在模块的所占角度
                    var currentArea = (i + 1) * everyAngle;

                    if (currentAngle <= 90) {
                        return -currentAngle;
                    } else if (currentAngle <= 180 && currentAngle > 90) {
                        return 180 - currentAngle;
                    } else if (currentAngle < 270 && currentAngle > 180) {
                        return 180 - currentAngle;
                    } else if (currentAngle < 360 && currentAngle >= 270) {
                        return 360 - currentAngle;
                    }
                }

                //有值的色图的正切处理
                var data3 = [];
                data3 = JSON.parse(JSON.stringify(data));
                for (var i = 0; i < data3.length; i++) {
                    if (i === 0) {
                        data3[i]['label']['color'] = '#333';
                        data3[i]['itemStyle']['color'] = 'rgba(25, 255, 224)';
                        data3[i]['emphasis']['itemStyle']['color'] = 'rgba(25, 255, 224)';
                        data3[i]['label']['rotate'] = angleText(i, data3.length);
                    } else {
                        data3[i]['label']['color'] = '#fff';
                        data3[i]['itemStyle']['color'] = '#4169E1';
                        data3[i]['emphasis']['itemStyle']['color'] = '#6A5ACD';
                        data3[i]['label']['rotate'] = angleText(i, data3.length);
                    }
                }


                //最外层大圈的数据
                var data1 = [];

                data1 = JSON.parse(JSON.stringify(data));
                for (var i = 0; i < data1.length; i++) {
                    data1[i].value = 1;
                    data1[i]['label']['rotate'] = angleText(i, data1.length);
                    if (i === 0) {
                        data1[i]['label']['color'] = 'rgba(25, 255, 224)';
                    }
                }

                //透明饼图的数据
                var data2 = [];

                for (var i = 0; i < data.length; i++) {
                    if (i === 0) {
                        data2.push({
                            value: 1,
                            itemStyle: {
                                color: 'rgba(25, 255, 224,0.05)',
                            }
                        });
                    } else {
                        data2.push({
                            value: 1,
                            itemStyle: {
                                color: 'transparent',
                            }
                        });
                    }
                }


                var option = {
                    title: {
                        text: '出行推荐',
                        left: "center",
                        top: 'center',
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    backgroundColor: '#051b4a',
                    grid: {},
                    polar: {},
                    angleAxis: {
                        show: false,
                        interval: 1,
                        type: 'category',
                        data: [],


                    },
                    //中间画圈圈的坐标轴
                    radiusAxis: {
                        show: false
                    },
                    series: [{
                        type: 'pie',
                        radius: ["68%", "90%"],
                        hoverAnimation: false,
                        itemStyle: {
                            color: 'transparent'
                        },
                        labelLine: {
                            normal: {
                                show: false,
                                length: 30,
                                length2: 55
                            },
                        },
                        label: {
                            normal: {
                                position: 'inside',
                                align: 'right'
                            }
                        },
                        name: "",
                        data: data1
                    }, {
                        stack: 'a',
                        type: 'pie',
                        radius: ['75%', '42%'],
                        roseType: 'area',
                        zlevel: 10,
                        itemStyle: {
                            color: '#4169E1',
                        },
                        emphasis: {
                            itemStyle: {
                                color: '#6A5ACD'
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                textStyle: {
                                    fontSize: 15,
                                    color: '#fff'
                                },
                                position: 'inside',
                                rotate: 30,
                                align: 'right',
                                fontWeight: 'bold',
                                formatter: '距您{c}米'
                            },
                            emphasis: {
                                show: true
                            }
                        },
                        animation: false,
                        data: data3
                    }, {
                        type: 'pie',
                        zlevel: 99,
                        radius: ["15%", "90%"],
                        selectedOffset: 0,
                        animation: false,
                        hoverAnimation: false,
                        label: {
                            normal: {
                                show: false,
                            }
                        },
                        data: data2
                    }]
                };
                var myChart = echarts.init(document.querySelector('.average-chart'));
                myChart.setOption(option);

                myChart.on('click', function(a) {
                    //最外层的字体颜色重置变色
                    for (var da1 = 0; da1 < option.series[0].data.length; da1++) {
                        option.series[0].data[da1].label.color = '#fff';
                    }

                    //色圈的字体颜色和选中颜色重置
                    for (var da2 = 0; da2 < option.series[1].data.length; da2++) {
                        option.series[1].data[da2].itemStyle.color = '#4169E1';
                        option.series[1].data[da2].label.color = '#fff';
                        //hover颜色重置
                        option.series[1].data[da2].emphasis.itemStyle.color = '#6A5ACD';

                    }

                    //背景的透明饼图的重置
                    for (var da3 = 0; da3 < option.series[2].data.length; da3++) {
                        option.series[2].data[da3].itemStyle.color = 'transparent';
                    }

                    option.series[1].data[a.dataIndex].itemStyle.color = 'rgba(25, 255, 224)';
                    option.series[1].data[a.dataIndex].label.color = '#333';
                    //hover 颜色改变
                    option.series[1].data[a.dataIndex].emphasis.itemStyle.color = 'rgba(25, 255, 224)';
                    option.series[0].data[a.dataIndex].label.color = 'rgba(25, 255, 224)';
                    option.series[2].data[a.dataIndex].itemStyle.color = 'rgba(25, 255, 224,0.1)';
                    //console.log(option)
                    myChart.setOption(option);
                });

                myChart.on('mouseover', function(a) {
                    myChart.dispatchAction({
                        type: 'highlight',
                        seriesIndex: 1,
                        dataIndex: a.dataIndex
                    });
                });

                myChart.on('mouseout', function(a) {
                    myChart.dispatchAction({
                        type: 'downplay',
                        seriesIndex: 1,
                        dataIndex: a.dataIndex
                    });
                });
            },
            showRecommend(item) {
                var that = this;
                $('.average').css('display', 'block');
                $('.average>div').addClass('animated fadeIn');
                var time = new Date();
                var currentTime = '20181003' + time.getHours() + time.getMinutes() + time.getSeconds();
                $.ajax({
                    url:'http://121.199.31.199:8081/mode?imsi='+item.id+'&timestamp=' +currentTime,
                    // url: 'http://121.199.31.199:8081/mode?imsi=460070095025648611&timestamp=20181003185953',
                    type: 'get',
                    success: function(res) {
                        var data = [];
                        for (var key in res) {
                            data.push({
                                name: key,
                                value: res[key],
                                label: {
                                    color: '#fff'
                                },
                                itemStyle: {

                                },
                                emphasis: {
                                    itemStyle: {

                                    }
                                }
                            })
                        }
                        console.log(res);
                        console.log(data);
                        that.getChart(data);
                    }
                })
            },
            showLine(item) {
                var lines = [];
                var option = {};
                var res = [];
                option = this.myChart.getOption();
                console.log(item);
                $('.info p').html('id：' + item.id)
                for (let i = 0; i < item.path.length; i++) {
                    if (item.state[i] == '驻留') {
                        res.push({
                            name: '驻留',
                            value: item.path[i].concat(20)
                        })
                    }
                }
                option.series.length = 2;
                option.series.push({
                    type: 'scatter',
                    coordinateSystem: 'bmap',
                    symbol: 'pin', //气泡
                    symbolSize: 60,
                    itemStyle: {
                        normal: {
                            color: 'turquoise', //标志颜色
                        }
                    },
                    label: {
                        normal: {
                            show: true,
                            textStyle: {
                                color: '#fff',
                                fontSize: 12,
                                fontWeight: 1000
                            },
                            formatter(value) {
                                return '驻留';
                            }
                        }
                    },
                    data: res
                });
                option.series.push({
                    type: 'effectScatter',
                    symbolSize: 10,
                    coordinateSystem: 'bmap',
                    itemStyle: {
                        normal: {
                            color: 'turquoise', //标志颜色
                        }
                    },
                    data: res
                });
                lines.push({
                    coords: item.path,
                    lineStyle: {
                        normal: {
                            color: 'tomato'
                        }
                    }
                });
                option.bmap[0].center = item.path[0];
                option.bmap[0].zoom = 13;
                console.log(option)
                option.series[0].data = lines;
                option.series[1].data = lines;
                this.myChart.setOption(option)
            }
        },
        data() {
            return {
                res: [],
                myChart: null,
                activeName: '1'
            }
        },
        mounted() {
            var data = [{
                value: 100,
                name: 'Chrome移动版',
                label: {
                    color: '#fff'
                },
                itemStyle: {

                },
                emphasis: {
                    itemStyle: {

                    }
                }
            }, {
                value: 80,
                name: 'Google Chrome',
                label: {
                    color: '#fff'
                },
                itemStyle: {

                },
                emphasis: {
                    itemStyle: {

                    }
                }
            }];
            var that = this;
            var dataHeight = $('.data').height();
            var marginH = $('.datalist-title').height();
            $('.data-content').height(dataHeight - 75 - marginH);
            $('.data-content').css('top', marginH + 67 + 'px');
            $('.nav-top p').html('人口出行分析');
            this.myChart = echarts.init(document.getElementById('chart'));
            this.myChart.showLoading();

            var lines = [{
                coords: [
                    [114.125453, 22.537998],
                    [114.125753, 22.538038],
                    [114.125763, 22.537928]
                ],
                lineStyle: {
                    normal: {
                        color: 'red'
                    }
                }

            }];
            var colors = ['#EEEC0D', '#36FFF8', '#67D670', '#FC92F7', '#FC92F7', 'tomato', 'orange', 'green', 'pink', 'lightblue'];
            var option = {
                    bmap: {
                        center: [123.42, 41.82],
                        zoom: 12,
                        roam: true,
                        mapStyle: {
                            'styleJson': [{
                                'featureType': 'water',
                                'elementType': 'all',
                                'stylers': {
                                    'color': '#031628'
                                }
                            }, {
                                'featureType': 'land',
                                'elementType': 'geometry',
                                'stylers': {
                                    'color': '#000102'
                                }
                            }, {
                                'featureType': 'highway',
                                'elementType': 'all',
                                'stylers': {
                                    'visibility': 'off'
                                }
                            }, {
                                'featureType': 'arterial',
                                'elementType': 'geometry.fill',
                                'stylers': {
                                    'color': '#000000'
                                }
                            }, {
                                'featureType': 'arterial',
                                'elementType': 'geometry.stroke',
                                'stylers': {
                                    'color': '#0b3d51'
                                }
                            }, {
                                'featureType': 'local',
                                'elementType': 'geometry',
                                'stylers': {
                                    'color': '#000000'
                                }
                            }, {
                                'featureType': 'railway',
                                'elementType': 'geometry.fill',
                                'stylers': {
                                    'color': '#000000'
                                }
                            }, {
                                'featureType': 'railway',
                                'elementType': 'geometry.stroke',
                                'stylers': {
                                    'color': '#08304b'
                                }
                            }, {
                                'featureType': 'subway',
                                'elementType': 'geometry',
                                'stylers': {
                                    // 'color': '#000000',// 默认透明显示地铁线路
                                    'lightness': -70
                                }
                            }, {
                                'featureType': 'building',
                                'elementType': 'geometry.fill',
                                'stylers': {
                                    'color': '#000000'
                                }
                            }, {
                                'featureType': 'all',
                                'elementType': 'labels.text.fill',
                                'stylers': {
                                    'color': '#857f7f'
                                }
                            }, {
                                'featureType': 'all',
                                'elementType': 'labels.text.stroke',
                                'stylers': {
                                    'color': '#000000'
                                }
                            }, {
                                'featureType': 'building',
                                'elementType': 'geometry',
                                'stylers': {
                                    'color': '#022338'
                                }
                            }, {
                                'featureType': 'green',
                                'elementType': 'geometry',
                                'stylers': {
                                    'color': '#062032'
                                }
                            }, {
                                'featureType': 'boundary',
                                'elementType': 'all',
                                'stylers': {
                                    'color': '#465b6c'
                                }
                            }, {
                                'featureType': 'manmade',
                                'elementType': 'all',
                                'stylers': {
                                    'color': '#022338'
                                }
                            }, {
                                'featureType': 'label',
                                'elementType': 'all',
                                'stylers': {
                                    'visibility': 'off'
                                }
                            }]
                        }
                    },
                    series: [{
                        type: 'lines',
                        coordinateSystem: 'bmap',
                        polyline: true,
                        data: lines,
                        silent: true,
                        lineStyle: {
                            normal: {
                                opacity: 0.4,
                                width: 2.5
                            }
                        },
                        progressiveThreshold: 500,
                        progressive: 200
                    }, {
                        type: 'lines',
                        coordinateSystem: 'bmap',
                        polyline: true,
                        data: lines,
                        lineStyle: {
                            normal: {
                                width: 0
                            }
                        },
                        effect: {
                            constantSpeed: 50,
                            show: true,
                            trailLength: 0.6,
                            symbolSize: 3
                        },
                        zlevel: 1
                    }]
                }
                //构建地图
            $.ajax({
                url: 'http://116.62.79.166:8003/traffic/personalAll',
                type: 'get',
                success: function(data) {
                    that.res = data;
                    console.log(data);
                    that.
                    lines = [];
                    for (let i = 0; i < data.length; i++) {
                        lines.push({
                            coords: data[i].path,
                            lineStyle: {
                                normal: {
                                    color: colors[i % 10]
                                }
                            }
                        });
                    }
                    option.series[0].data = lines;
                    option.series[1].data = lines;
                    that.myChart.setOption(option);
                    that.myChart.hideLoading();
                }
            });
            //获取全部出行数据
            $('.all').on('click', function() {
                $('.info p').html('全部人口');
                that.myChart.showLoading();
                that.myChart.getOption().series.length = 2;
                $.ajax({
                    url: 'http://116.62.79.166:8003/traffic/personalAll',
                    type: 'get',
                    success: function(data) {
                        that.res = data;
                        console.log(data);
                        lines = [];
                        for (let i = 0; i < data.length; i++) {
                            lines.push({
                                coords: data[i].path,
                                lineStyle: {
                                    normal: {
                                        color: colors[i % 10]
                                    }
                                }
                            });
                        }
                        option.series.length = 2;
                        option.series[0].data = lines;
                        option.series[1].data = lines;
                        that.myChart.setOption(option, true);
                        that.myChart.hideLoading();
                    }
                })
            });
            //获取夜行数据
            $('.yx').on('click', function() {
                $('.info p').html('夜行人口');
                that.myChart.showLoading();
                that.myChart.getOption().series.length = 2;
                $.ajax({
                    url: 'http://116.62.79.166:8003/traffic/personalNight',
                    type: 'get',
                    success: function(data) {
                        that.res = data;
                        console.log(data);
                        lines = [];
                        for (let i = 0; i < data.length; i++) {
                            lines.push({
                                coords: data[i].path,
                                lineStyle: {
                                    normal: {
                                        color: colors[i % 10]
                                    }
                                }
                            });
                        }
                        option.series.length = 2;
                        option.series[0].data = lines;
                        option.series[1].data = lines;
                        that.myChart.setOption(option, true);
                        that.myChart.hideLoading();
                    }
                })
            });
        }
    }
</script>

<style scoped>
    .rkcx {
        position: fixed;
        height: 100%;
        width: 100%;
        background: #051b4a;
        background: url('../assets/bg.png');
        background-size: 100% 100%;
    }
    
    .chart-container {
        position: fixed;
        width: 60%;
        left: 19.5%;
        height: 76%;
        top: 12%;
        z-index: -1;
        border-radius: 1px;
        border: 2px solid rgb(59, 73, 134);
    }
    
    #chart {
        position: absolute;
        height: 100%;
        width: 100%;
    }
    
    .chart-container img {
        vertical-align: middle;
    }
    
    .top-left {
        position: absolute;
        left: -3px;
        top: -7px;
        transform: rotateX(180deg);
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .info {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 250px;
        height: 80px;
        background: rgba(41, 54, 128, 0.486);
        background-size: 100% 100%;
        border-top-left-radius: 10px;
        color: white;
        font-weight: 1000;
        font-size: 14px;
        border-left: rgba(127, 140, 255, 0.719) 2px solid;
        border-top: rgba(127, 140, 255, 0.719) 2px solid;
    }
    
    .info p {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        width: 100%;
        margin: 0;
    }
    
    .top-right {
        position: absolute;
        right: -3px;
        top: -7px;
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .bottom-right {
        position: absolute;
        right: -3px;
        bottom: -7px;
        transform: rotateX(180deg);
        filter: hue-rotate(57deg);
        pointer-events: none;
        z-index: 99;
    }
    
    .bottom-left {
        position: absolute;
        left: -3px;
        bottom: -7px;
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .data {
        position: fixed;
        height: 80%;
        width: 18%;
        right: 0px;
        background: url('../assets/data.png');
        background-size: 100% 100%;
        top: 10%;
        z-index: -1;
    }
    
    .data-title {
        background: none;
        border: none;
        background-image: url(../assets/data-bg.png);
        background-size: 100% 100%;
        width: 230px;
        height: 40px;
        padding-bottom: 7px;
        position: relative;
        color: white;
        outline: none;
        color: turquoise;
        font-weight: 1000;
        opacity: 0.9;
        top: 20px;
    }
    
    .datalist {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    
    .datalist>p {
        width: 33.3%;
        flex-grow: 1;
        text-align: center;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    
    .p0 {
        opacity: 0;
    }
    
    .datalist>p:nth-last-of-type(1) {
        transform: translateY(-50%);
        margin-top: -12px;
    }
    
    .datalist>p:nth-of-type(1) {
        transform: none;
        margin-top: 10px;
    }
    
    .datalist-title {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 90%;
        margin-left: 5%;
        color: white;
        border: 1.5px solid rgba(127, 255, 212, 0.377);
        background: linear-gradient(to left, #355288, #0f378f);
        margin-top: 25px;
        align-items: center;
    }
    
    .datalist-title p {
        margin: 10px;
        font-weight: 1000;
        font-size: 14px;
    }
    
     ::-webkit-scrollbar {
        display: none;
    }
    
    .data-content {
        position: absolute;
        width: 100%;
        top: 67px;
        overflow: scroll;
        scrollbar-width: none;
        /* Firefox */
        -ms-overflow-style: none;
        /* IE 10+ */
    }
    
    .btn {
        position: fixed;
        left: 50%;
        height: 50px;
        bottom: 10px;
        width: 580px;
        transform: translateX(-50%);
    }
    
    .btn button:hover {
        color: rgba(127, 255, 212, 0.719);
    }
    
    .btn button {
        color: white;
        font-weight: 1000;
        font-size: 13px;
        outline: none;
        background-image: linear-gradient(to left, #355288, #0f378f);
        border-radius: 10px;
        border: rgba(127, 255, 212, 0.719) 1.5px solid;
        box-shadow: 0 0 1px 1px #313ac7;
        width: 180px;
        height: 45px;
        margin-left: 20px;
        margin-right: 20px;
    }
    
    .time-p {
        position: relative;
    }
    
    .time-p::after {
        display: inline-block;
        height: 8px;
        width: 8px;
        border-radius: 50%;
        background: #4397ff;
        border: 2px solid #fff;
        color: transparent;
        content: '1';
        position: absolute;
        left: 0%;
        top: 50%;
        transform: translateY(-50%);
    }
    
    .timeline {
        position: relative;
    }
    
    .timeline::before {
        content: '1';
        color: transparent;
        display: inline-block;
        background: #4397ff;
        position: absolute;
        left: -100%;
        height: 100%;
        width: 3px;
        margin-left: 4px;
        border-radius: 10rem;
    }
    
    .average {
        position: fixed;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.37);
        z-index: 999;
    }
    
    .average-chart {
        position: absolute;
        height: 60%;
        width: 50%;
        /* background: red; */
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        border-radius: 20px;
        border: 5px solid rgba(127, 255, 212, 0.685);
        overflow: hidden;
    }
    
    .back {
        position: absolute;
        right: 26%;
        top: 22%;
        font-size: 40px;
        color: white;
        cursor: pointer;
        z-index: 999;
    }
    
    /deep/ .el-collapse-item__header {
        width: 85%;
        margin-left: 5%;
        border-radius: 10px;
        border: 1.5px solid rgba(127, 255, 212, 0.377) !important;
        box-shadow: none;
        margin-top: 5px;
        background: linear-gradient(to left, #355288, #0f378f);
        color: white;
        padding-left: 5%;
        font-weight: 1000;
        font-size: 13px;
    }
    
    /deep/ .el-collapse {
        border: none;
    }
    
    /deep/ .el-collapse-item__wrap {
        background: linear-gradient(to left, #355288, #0f378f);
        border: none;
        width: 80%;
        margin-left: 10%;
        border-left: 1.5px solid rgba(127, 255, 212, 0.377);
        border-right: 1.5px solid rgba(127, 255, 212, 0.377);
        border-bottom: 1.5px solid rgba(127, 255, 212, 0.377);
    }
    
    /deep/ .el-collapse-item__content {
        color: white;
        width: 90%;
        margin-left: 5%;
        padding-bottom: 0;
    }
</style>


