<template>
    <div>
        <div class="bg"></div>
        <canvas id="canvas"></canvas> 
        <div class="enter">
            <div class="enter-top-left"><img src="@/assets/enter-top-left.jpg" alt="" width="100px" class="animated fadeIn"></div>
            <div class="enter-top-middle animated bounceUp"><img src="@/assets/enter-top-middle.jpg" alt="" width="100px"></div>
            <div class="enter-top-border-1 animated bounceInLeft"><img src="@/assets/enter-border-1.jpg" alt="" width="250px"></div>
            <div class="enter-top-border-2 animated bounceInRight"><img src="@/assets/enter-border-2.jpg" alt="" width="250px"></div>
        </div>
        <a class="transparent" href="#/rkmd">
            <p><span class="bg"></span><span class="base"></span><span class="text">进入平台</span></p>
        </a>
    </div>
</template>

<script>
export default {
    beforeDestroy(){
        var element = document.querySelector('canvas');
        element.remove();
    },
    mounted(){
        var that = this;
        var canvas = document.getElementById('canvas'),
        ctx = canvas.getContext('2d'),
        w = canvas.width = window.innerWidth,
        h = canvas.height = window.innerHeight,
        hue = 217,
        // 177
        stars = [],
        count = 0,
        maxStars = 1300;//星星数量

        var canvas2 = document.createElement('canvas'),
        ctx2 = canvas2.getContext('2d');
        canvas2.width = 100;
        canvas2.height = 100;
        var half = canvas2.width / 2,
        gradient2 = ctx2.createRadialGradient(half, half, 0, half, half, half);
        gradient2.addColorStop(0.025, '#CCC');
        gradient2.addColorStop(0.1, 'hsl(' + hue + ', 61%, 33%)');
        gradient2.addColorStop(0.25, 'hsl(' + hue + ', 64%, 6%)');
        gradient2.addColorStop(1, 'transparent');

        ctx2.fillStyle = gradient2;
        ctx2.beginPath();
        ctx2.arc(half, half, half, 0, Math.PI * 2);
        ctx2.fill();

        // End cache

        function random(min, max) {
        if (arguments.length < 2) {
            max = min;
            min = 0;
        }

        if (min > max) {
            var hold = max;
            max = min;
            min = hold;
        }

        return Math.floor(Math.random() * (max - min + 1)) + min;
        }

        function maxOrbit(x, y) {
        var max = Math.max(x, y),
            diameter = Math.round(Math.sqrt(max * max + max * max));
        return diameter / 2;
        //星星移动范围，值越大范围越小，
        }

        var Star = function() {

        this.orbitRadius = random(maxOrbit(w, h));
        this.radius = random(60, this.orbitRadius) / 8; 
        //星星大小
        this.orbitX = w / 2 ;
        this.orbitY = h / 2;
        this.timePassed = random(0, maxStars);
        this.speed = random(this.orbitRadius) / 50000; 
        //星星移动速度
        this.alpha = random(2, 10) / 10;

        count++;
        stars[count] = this;
        }

        Star.prototype.draw = function() {
        var x = Math.sin(this.timePassed) * this.orbitRadius + this.orbitX,
            y = Math.cos(this.timePassed) * this.orbitRadius + this.orbitY,
            twinkle = random(10);

        if (twinkle === 1 && this.alpha > 0) {
            this.alpha -= 0.05;
        } else if (twinkle === 2 && this.alpha < 1) {
            this.alpha += 0.05;
        }

        ctx.globalAlpha = this.alpha;
        ctx.drawImage(canvas2, x - this.radius / 2, y - this.radius / 2, this.radius, this.radius);
        this.timePassed += this.speed;
        }

        for (var i = 0; i < maxStars; i++) {
        new Star();
        }

        function animation() {
        ctx.globalCompositeOperation = 'source-over';
        ctx.globalAlpha = 0.5; //尾巴
        ctx.fillStyle = 'hsla(' + hue + ', 64%, 6%, 2)';
        ctx.fillRect(0, 0, w, h)

        ctx.globalCompositeOperation = 'lighter';
        for (var i = 1, l = stars.length; i < l; i++) {
            stars[i].draw();
        };

        window.requestAnimationFrame(animation);
        }

        animation();





        /// 粒子字幕
        const STEP_LENGTH = 1;
        const CELL_SIZE = 4;
        const BORDER_WIDTH = 1;
        const MAX_FONT_SIZE = 800;
        const MAX_ELECTRONS = 200;
        const CELL_DISTANCE = CELL_SIZE + BORDER_WIDTH;

        // shorter for brighter paint
        // be careful of performance issue
        const CELL_REPAINT_INTERVAL = [
        300, // from
        500 // to
        ];

        const BG_COLOR = '#003349';
        const BORDER_COLOR = '#002B3D';
        const CELL_HIGHLIGHT = '#3DE3C9';
        const ELECTRON_COLOR = '#3DE3C9';
        const FONT_COLOR = '#3DE3C9';

        const FONT_FAMILY = '"Helvetica Neue", Arial, sans-serif';

        const DPR = window.devicePixelRatio || 1;

        const ACTIVE_ELECTRONS = [];
        const PINNED_CELLS = [];

        const MOVE_TRAILS = [
        [0, 1], // down
        [0, -1], // up
        [1, 0], // right
        [-1, 0] // left
        ].map(([x, y]) => [x * CELL_DISTANCE, y * CELL_DISTANCE]);

        const END_POINTS_OFFSET = [
        [0, 0], // left top
        [0, 1], // left bottom
        [1, 0], // right top
        [1, 1] // right bottom
        ].map(([x, y]) => [
        x * CELL_DISTANCE - BORDER_WIDTH / 2,
        y * CELL_DISTANCE - BORDER_WIDTH / 2]);


        class FullscreenCanvas {
        constructor(disableScale = false) {
        const canvas = document.createElement('canvas');
        const context = canvas.getContext('2d');

        this.canvas = canvas;
        this.context = context;
        this.disableScale = disableScale;

        this.resizeHandlers = [];
        this.handleResize = _.debounce(this.handleResize.bind(this), 100);

        this.adjust();

        window.addEventListener('resize', this.handleResize);
        }

        adjust() {
            if(that.$route.path!='/'){
                return;
            }
            const {
            canvas,
            context,
            disableScale } =
            this;

            // const {
            // innerWidth,
            // innerHeight } =
            // window;
            const innerHeight = document.querySelector('.enter').clientHeight - 100;
            const innerWidth = document.querySelector('.enter').clientWidth -100;

            this.width = innerWidth;
            this.height = innerHeight;

            const scale = disableScale ? 1 : DPR;

            this.realWidth = canvas.width = innerWidth * scale;
            this.realHeight = canvas.height = innerHeight * scale;
            canvas.style.width = `${innerWidth}px`;
            canvas.style.height = `${innerHeight}px`;
            canvas.style.marginTop = '50px';
            canvas.style.marginLeft = '50px';
            canvas.style.opacity = '.9';
            canvas.style.borderRadius = '10px';
            canvas.style.borderRadius = '10px';
            canvas.style.boxShadow = 'rgb(17, 156, 169) 0px 0px 0px 4px';
            canvas.style.animationDelay = '1s';
            canvas.style.animationDuration = '2.5s';
            canvas.classList.add('animated');
            canvas.classList.add('fadeIn');
            context.scale(scale, scale);
        }

        clear() {
            const { context } = this;
            context.clearRect(0, 0, this.width, this.height);
        }

        makeCallback(fn) {
            fn(this.context, this);
        }

        blendBackground(background, opacity = 0.05) {
            return this.paint((ctx, { realWidth, realHeight, width, height }) => {
            ctx.globalCompositeOperation = 'source-over';
            ctx.globalAlpha = opacity;

            ctx.drawImage(background, 0, 0, realWidth, realHeight, 0, 0, width, height);
            });
        }

        paint(fn) {
            if (!_.isFunction(fn)) return;

            const { context } = this;

            context.save();

            this.makeCallback(fn);

            context.restore();

            return this;
        }

        repaint(fn) {
            if (!_.isFunction(fn)) return;

            this.clear();

            return this.paint(fn);
        }

        onResize(fn) {
            if (!_.isFunction(fn)) return;

            this.resizeHandlers.push(fn);
        }

        handleResize() {
            const { resizeHandlers } = this;

            if (!resizeHandlers.length) return;

            this.adjust();

            resizeHandlers.forEach(this.makeCallback.bind(this));
        }

        renderIntoView(target = document.body) {
            const { canvas } = this;

            this.container = target;

            canvas.style.position = 'absolute';
            canvas.style.left = '25%';
            canvas.style.top = '25%';

            target.appendChild(canvas);
        }

        remove() {
            if (!this.container) return;

            try {
            window.removeEventListener('resize', this.handleResize);
            this.container.removeChild(this.canvas);
            } catch (e) {}
        }}


        class Electron {
        constructor(
        x = 0,
        y = 0,
        {
            lifeTime = 3 * 1e3,
            speed = STEP_LENGTH,
            color = ELECTRON_COLOR } =
        {})
        {
            this.lifeTime = lifeTime;
            this.expireAt = Date.now() + lifeTime;

            this.speed = speed;
            this.color = color;

            this.radius = BORDER_WIDTH / 2;
            this.current = [x, y];
            this.visited = {};
            this.setDest(this.randomPath());
        }

        randomPath() {
            const {
            current: [x, y] } =
            this;

            const { length } = MOVE_TRAILS;

            const [deltaX, deltaY] = MOVE_TRAILS[_.random(length - 1)];

            return [
            x + deltaX,
            y + deltaY];

        }

        composeCoord(coord) {
            return coord.join(',');
        }

        hasVisited(dest) {
            const key = this.composeCoord(dest);

            return this.visited[key];
        }

        setDest(dest) {
            this.destination = dest;
            this.visited[this.composeCoord(dest)] = true;
        }

        next() {
            let {
            speed,
            current,
            destination } =
            this;

            if (Math.abs(current[0] - destination[0]) <= speed / 2 &&
            Math.abs(current[1] - destination[1]) <= speed / 2)
            {
            destination = this.randomPath();

            let tryCnt = 1;
            const maxAttempt = 4;

            while (this.hasVisited(destination) && tryCnt <= maxAttempt) {
                tryCnt++;
                destination = this.randomPath();
            }

            this.setDest(destination);
            }

            const deltaX = destination[0] - current[0];
            const deltaY = destination[1] - current[1];

            if (deltaX) {
            current[0] += deltaX / Math.abs(deltaX) * speed;
            }

            if (deltaY) {
            current[1] += deltaY / Math.abs(deltaY) * speed;
            }

            return [...this.current];
        }

        paintNextTo(layer = new FullscreenCanvas()) {
            const {
            radius,
            color,
            expireAt,
            lifeTime } =
            this;

            const [x, y] = this.next();

            layer.paint(ctx => {
            ctx.globalAlpha = Math.max(0, expireAt - Date.now()) / lifeTime;
            ctx.fillStyle = color;
            ctx.shadowColor = color;
            ctx.shadowBlur = radius * 5;
            ctx.globalCompositeOperation = 'lighter';

            ctx.beginPath();
            ctx.arc(x, y, radius, 0, Math.PI * 2);
            ctx.closePath();

            ctx.fill();
            });
        }}


        class Cell {
        constructor(
        row = 0,
        col = 0,
        {
            electronCount = _.random(1, 4),
            background = ELECTRON_COLOR,
            forceElectrons = false,
            electronOptions = {} } =
        {})
        {
            this.background = background;
            this.electronOptions = electronOptions;
            this.forceElectrons = forceElectrons;
            this.electronCount = Math.min(electronCount, 4);

            this.startY = row * CELL_DISTANCE;
            this.startX = col * CELL_DISTANCE;
        }

        delay(ms = 0) {
            this.pin(ms * 1.5);
            this.nextUpdate = Date.now() + ms;
        }

        pin(lifeTime = -1 >>> 1) {
            this.expireAt = Date.now() + lifeTime;

            PINNED_CELLS.push(this);
        }

        scheduleUpdate(
        t1 = CELL_REPAINT_INTERVAL[0],
        t2 = CELL_REPAINT_INTERVAL[1])
        {
            this.nextUpdate = Date.now() + _.random(t1, t2);
        }

        paintNextTo(layer = new FullscreenCanvas()) {
            const {
            startX,
            startY,
            background,
            nextUpdate } =
            this;

            if (nextUpdate && Date.now() < nextUpdate) return;

            this.scheduleUpdate();
            this.createElectrons();

            layer.paint(ctx => {
            ctx.globalCompositeOperation = 'lighter';
            ctx.fillStyle = background;
            ctx.fillRect(startX, startY, CELL_SIZE, CELL_SIZE);
            });
        }

        popRandom(arr = []) {
            const ramIdx = _.random(arr.length - 1);

            return arr.splice(ramIdx, 1)[0];
        }

        createElectrons() {
            const {
            startX,
            startY,
            electronCount,
            electronOptions,
            forceElectrons } =
            this;

            if (!electronCount) return;

            const endpoints = [...END_POINTS_OFFSET];

            const max = forceElectrons ? electronCount : Math.min(electronCount, MAX_ELECTRONS - ACTIVE_ELECTRONS.length);

            for (let i = 0; i < max; i++) {
            const [offsetX, offsetY] = this.popRandom(endpoints);

            ACTIVE_ELECTRONS.push(new Electron(
            startX + offsetX,
            startY + offsetY,
            electronOptions));

            }
        }}


        const bgLayer = new FullscreenCanvas();
        const mainLayer = new FullscreenCanvas();
        const shapeLayer = new FullscreenCanvas(true);

        function stripOld(limit = 1000) {
        const now = Date.now();

        for (let i = 0, max = ACTIVE_ELECTRONS.length; i < max; i++) {
            const e = ACTIVE_ELECTRONS[i];

            if (e.expireAt - now < limit) {
            ACTIVE_ELECTRONS.splice(i, 1);

            i--;
            max--;
            }
        }
        }

        function createRandomCell(options = {}) {
        if (ACTIVE_ELECTRONS.length >= MAX_ELECTRONS) return;

        const { width, height } = mainLayer;

        const cell = new Cell(
        _.random(height / CELL_DISTANCE),
        _.random(width / CELL_DISTANCE),
        options);


        cell.paintNextTo(mainLayer);
        }

        function drawGrid() {
        bgLayer.paint((ctx, { width, height }) => {
            ctx.fillStyle = BG_COLOR;
            ctx.fillRect(0, 0, width, height);

            ctx.fillStyle = BORDER_COLOR;

            // horizontal lines
            for (let h = CELL_SIZE; h < height; h += CELL_DISTANCE) {
            ctx.fillRect(0, h, width, BORDER_WIDTH);
            }

            // vertical lines
            for (let w = CELL_SIZE; w < width; w += CELL_DISTANCE) {
            ctx.fillRect(w, 0, BORDER_WIDTH, height);
            }
        });
        }

        function iterateItemsIn(list) {
        const now = Date.now();

        for (let i = 0, max = list.length; i < max; i++) {
            const item = list[i];

            if (now >= item.expireAt) {
            list.splice(i, 1);
            i--;
            max--;
            } else {
            item.paintNextTo(mainLayer);
            }
        }
        }

        function drawItems() {
        iterateItemsIn(PINNED_CELLS);
        iterateItemsIn(ACTIVE_ELECTRONS);
        }

        let nextRandomAt;

        function activateRandom() {
        const now = Date.now();

        if (now < nextRandomAt) {
            return;
        }

        nextRandomAt = now + _.random(300, 1000);

        createRandomCell();
        }

        function handlePointer() {
        let lastCell = [];
        let touchRecords = {};

        function isSameCell(i, j) {
            const [li, lj] = lastCell;

            lastCell = [i, j];

            return i === li && j === lj;
        };

        function print(isMove, { clientX, clientY }) {
            const i = Math.floor(clientY / CELL_DISTANCE);
            const j = Math.floor(clientX / CELL_DISTANCE);

            if (isMove && isSameCell(i, j)) {
            return;
            }

            const cell = new Cell(i, j, {
            background: CELL_HIGHLIGHT,
            forceElectrons: true,
            electronCount: isMove ? 2 : 4,
            electronOptions: {
                speed: 3,
                lifeTime: isMove ? 500 : 1000,
                color: CELL_HIGHLIGHT } });



            cell.paintNextTo(mainLayer);
        }

        const handlers = {
            touchend({ changedTouches }) {
            if (changedTouches) {
                Array.from(changedTouches).forEach(({ identifier }) => {
                delete touchRecords[identifier];
                });
            } else {
                touchRecords = {};
            }
            } };


        function filterTouches(touchList) {
            return Array.from(touchList).filter(({ identifier, clientX, clientY }) => {
            const rec = touchRecords[identifier];
            touchRecords[identifier] = { clientX, clientY };

            return !rec || clientX !== rec.clientX || clientY !== rec.clientY;
            });
        }

        [
        'mousedown',
        'touchstart',
        'mousemove',
        'touchmove'].
        forEach(name => {
            const isMove = /move/.test(name);
            const isTouch = /touch/.test(name);

            const fn = print.bind(null, isMove);

            handlers[name] = function handler(evt) {
            if (isTouch) {
                filterTouches(evt.touches).forEach(fn);
            } else {
                fn(evt);
            }
            };
        });

        const events = Object.keys(handlers);

        events.forEach(name => {
            document.addEventListener(name, handlers[name]);
        });

        return function unbind() {
            events.forEach(name => {
            document.removeEventListener(name, handlers[name]);
            });
        };
        }

        function prepaint() {
        drawGrid();

        mainLayer.paint((ctx, { width, height }) => {
            // composite with rgba(255,255,255,255) to clear trails
            ctx.fillStyle = '#fff';
            ctx.fillRect(0, 0, width, height);
        });

        mainLayer.blendBackground(bgLayer.canvas, 0.9);
        }

        function render() {
        mainLayer.blendBackground(bgLayer.canvas);

        drawItems();
        activateRandom();

        shape.renderID = requestAnimationFrame(render);
        }

        const shape = {
        lastText: '',
        lastMatrix: null,
        renderID: undefined,
        isAlive: false,

        get electronOptions() {
            return {
            speed: 2,
            color: FONT_COLOR,
            lifeTime: _.random(300, 500) };

        },

        get cellOptions() {
            return {
            background: FONT_COLOR,
            electronCount: _.random(1, 4),
            electronOptions: this.electronOptions };

        },

        get explodeOptions() {
            return {
            ...this.cellOptions,
            electronOptions: {
                ...this.electronOptions,
                lifeTime: _.random(500, 1500) } };


        },

        init(container = document.body) {
            if (this.isAlive) {
            return;
            }

            bgLayer.onResize(drawGrid);
            mainLayer.onResize(prepaint);

            mainLayer.renderIntoView(container);

            shapeLayer.onResize(() => {
            if (this.lastText) {
                this.print(this.lastText);
            }
            });

            prepaint();
            render();

            this.unbindEvents = handlePointer();
            this.isAlive = true;
        },

        clear() {
            const {
            lastMatrix } =
            this;

            this.lastText = '';
            this.lastMatrix = null;
            PINNED_CELLS.length = 0;

            if (lastMatrix) {
            this.explode(lastMatrix);
            }
        },

        destroy() {
            if (!this.isAlive) {
            return;
            }

            bgLayer.remove();
            mainLayer.remove();
            shapeLayer.remove();

            this.unbindEvents();

            cancelAnimationFrame(this.renderID);

            ACTIVE_ELECTRONS.length = PINNED_CELLS.length = 0;
            this.lastMatrix = null;
            this.lastText = '';
            this.isAlive = false;
        },

        getTextMatrix(
        text,
        {
            fontWeight = 'bold',
            fontFamily = FONT_FAMILY } =
        {})
        {
            const {
            width,
            height } =
            shapeLayer;

            shapeLayer.repaint(ctx => {
            ctx.textAlign = 'center';
            ctx.textBaseline = 'middle';
            ctx.font = `${fontWeight} ${MAX_FONT_SIZE}px ${fontFamily}`;

            const scale = width / ctx.measureText(text).width;
            const fontSize = Math.min(MAX_FONT_SIZE, MAX_FONT_SIZE * scale * 0.8);

            ctx.font = `${fontWeight} ${fontSize}px ${fontFamily}`;

            ctx.fillText(text, width / 2, height / 2);
            });

            const pixels = shapeLayer.context.getImageData(0, 0, width, height).data;
            const matrix = [];

            for (let i = 0; i < height; i += CELL_DISTANCE) {
            for (let j = 0; j < width; j += CELL_DISTANCE) {
                const alpha = pixels[(j + i * width) * 4 + 3];

                if (alpha > 0) {
                matrix.push([
                Math.floor(i / CELL_DISTANCE),
                Math.floor(j / CELL_DISTANCE)]);

                }
            }
            }

            return matrix;
        },

        print(text, options) {
            const isBlank = !!this.lastText;

            this.clear();

            if (text !== 0 && !text) {
            if (isBlank) {
                // release
                this.spiral({
                reverse: true,
                lifeTime: 500,
                electronCount: 2 });

            }

            return;
            }

            this.spiral();

            this.lastText = text;

            const matrix = this.lastMatrix = _.shuffle(this.getTextMatrix(text, options));

            matrix.forEach(([i, j]) => {
            const cell = new Cell(i, j, this.cellOptions);

            cell.scheduleUpdate(200);
            cell.pin();
            });
        },

        spiral({
            radius,
            increment = 0,
            reverse = false,
            lifeTime = 250,
            electronCount = 1,
            forceElectrons = true } =
        {}) {
            const {
            width,
            height } =
            mainLayer;

            const cols = Math.floor(width / CELL_DISTANCE);
            const rows = Math.floor(height / CELL_DISTANCE);

            const ox = Math.floor(cols / 2);
            const oy = Math.floor(rows / 2);

            let cnt = 1;
            let deg = _.random(360);
            let r = radius === undefined ? Math.floor(Math.min(cols, rows) / 3) : radius;

            const step = reverse ? 15 : -15;
            const max = Math.abs(360 / step);

            while (cnt <= max) {
            const i = oy + Math.floor(r * Math.sin(deg / 180 * Math.PI));
            const j = ox + Math.floor(r * Math.cos(deg / 180 * Math.PI));

            const cell = new Cell(i, j, {
                electronCount,
                forceElectrons,
                background: CELL_HIGHLIGHT,
                electronOptions: {
                lifeTime,
                speed: 3,
                color: CELL_HIGHLIGHT } });




            cell.delay(cnt * 16);

            cnt++;
            deg += step;
            r += increment;
            }
        },

        explode(matrix) {
            stripOld();

            if (matrix) {
            const { length } = matrix;

            const max = Math.min(
            50,
            _.random(Math.floor(length / 20), Math.floor(length / 10)));


            for (let idx = 0; idx < max; idx++) {
                const [i, j] = matrix[idx];

                const cell = new Cell(i, j, this.explodeOptions);

                cell.paintNextTo(mainLayer);
            }
            } else {
            const max = _.random(10, 20);

            for (let idx = 0; idx < max; idx++) {
                createRandomCell(this.explodeOptions);
            }
            }
        } };


        let timer;

        function queue() {
        const text = '10 x 0';

        let i = 0;
        const max = text.length;

        const run = () => {
            if (i >= max) return;

            shape.print(text.slice(0, ++i));
            timer = setTimeout(run, 1e3 + i);
        };

        run();
        }

        function countdown() {
        const arr = _.range(3, 0, -1);

        let i = 0;
        const max = arr.length;

        const run = () => {
            if (i >= max) {
            shape.clear();
            return galaxy();
            }

            shape.print(arr[i++]);
            setTimeout(run, 1e3 + i);
        };

        run();
        }

        function galaxy() {
        shape.spiral({
            radius: 0,
            increment: 1,
            lifeTime: 100,
            electronCount: 1 });


        timer = setTimeout(galaxy, 16);
        }

        function ring() {
        shape.spiral();

        timer = setTimeout(ring, 16);
        }


        shape.init();
        shape.print('交通大数据');

        // prevent zoom
        document.addEventListener('touchmove', e => e.preventDefault());
    }
}
</script>

<style scoped>
.bg{
    position: fixed;
    width: 100%;
    height: 100%;
    background: url('../assets/index-bg.jpg');
    z-index: -1;
    background-size: 100% 100%; 
    filter: blur(1px);
} 
#canvas{
    opacity: .65;
}
.enter{
    position: fixed;
    height: 50%;
    width: 50%;
    left: 25%;
    top: 25%;
    background:rgba(19, 64, 85, 0.568);
    opacity: 0.9;
    border-bottom-right-radius: 10px;
    animation: showUp 1s both;
    animation-delay: 0.2s; 
}
.enter-top-left{
    position: absolute;
    left: 0;
    transform: translateX(-50%) translateY(-50%);
    animation: rotate 0.8s infinite linear;
    transform-origin: 0% 0%;
}
.bounceUp{
    animation-name:bounceUp; 
}
@keyframes showUp {
    0%{
        background:rgba(19, 64, 85, 0);
    }
    100%{
        background:rgba(19, 64, 85, 0.568);
    }
}
@keyframes bounceUp {
    0%,60%,75%,90%,100%{
        animation-timing-function: cubic-bezier(.215,.61,.355,1);
    }
    0%{
        transform: translate3d(0,-200px,0);
        opacity: 0;
    }
    60%{
        opacity: 1;
        transform: translate3d(0,-40%,0);
    }
    75%{
        transform: translate3d(0,-60%,0);
    }
    90%{
        transform: translate3d(0,-45%,0);
    }
    100%{
        transform: translateY(-50%);
    }
}
@keyframes rotate {
    0%{
        transform: rotate(0deg) translateX(-50%) translateY(-50%);
    }
    100%{
        transform: rotate(360deg) translateX(-50%) translateY(-50%);
    }
    
}
.enter-top-middle{
    position: absolute;
    top: 0;
    left: 100px;
    transform: translateY(-50%);
}
.enter img{
    vertical-align: middle;
    filter: brightness(.9);
}
.enter-top-border-1{
    position: absolute;
    left: 0;
    bottom: -5px;
}
.enter-top-border-2{
    position: absolute;
    right: 0;
    top: -5px;
    transform: rotate(180deg);
}
.text{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}
.btn{
    position: absolute;
    bottom: 50px;
    background-size:100% 100%;
    border: none;
    outline: none;
    color: white;
    padding: 20px;
    background: rgba(70, 199, 216, 0.418);
    left: 50%;
    transform: translateX(-50%);
    width: 200px;
    height: 60px;
}

/* 按钮特效 */
div a:after, div a:before {
  content: "";
  width: 1px;
  position: absolute;
  height: 8px;
  background: #0f1923;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
div a:before {
  right: 0;
  left: initial;
}
div a p {
  margin: 0;
  height: 54px;
  line-height: 54px;
  box-sizing: border-box;
  z-index: 1;
  left: 0;
  width: 100%;
  position: relative;
  overflow: hidden;
}
div a p span.base {
  box-sizing: border-box;
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  left: 0;
  border: 1px solid #16a8af;
}
div a p span.base:before {
  content: "";
  width: 2px;
  height: 2px;
  left: -1px;
  top: -1px;
  background: #0f1923;
  position: absolute;
  transition: 0.3s ease-out all;
}
div a p span.bg {
  left: -5%;
  position: absolute;
  background: #16a8af;
  width: 0;
  height: 100%;
  z-index: 3;
  transition: 0.3s ease-out all;
  transform: skewX(-10deg);
}
div a p span.text {
  z-index: 4;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: #16a7af34;
}
div a p span.text:after {
  content: "";
  width: 4px;
  height: 4px;
  right: 0;
  bottom: 0;
  background: #16a8af;
  position: absolute;
  transition: 0.3s ease-out all;
  z-index: 5;
}
div a:hover {
  color: #ece8e1;
}
div a:hover span.bg {
  width: 110%;
}
div a:hover span.text:after {
  background: #ece8e1;
}
div a {
  width: 100%;
  max-width: 240px;
  height: 54px;
  padding: 8px;
  font-size: 0.8rem;
  font-weight: 900;
  color: #1fe8f2;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  box-shadow: 0 0 0 1px inset #07565a;
  position: absolute;
  margin: 10px 0;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.transparent{
    cursor: pointer;
    animation: transUp 2s both ease;
    animation-delay: 2.5s;
}
@keyframes transUp {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
        transform: translateX(-50%);
    }
}
.text{
    font-size: 17px;
}
</style>


