<template>
    <div class="jzdfx">
        <Home></Home> 
        <div class="chart-container">
            <div id="chart"></div>
            <div class="top-left"><img src="@/assets/enter-border-1.jpg" alt="" width="350px"></div>
            <div class="bottom-left"><img src="@/assets/enter-border-1.jpg" alt="" width="350px"></div>
            <div class="top-right"><img src="@/assets/enter-border-2.jpg" alt="" width="350px"></div>
            <div class="bottom-right"><img src="@/assets/enter-border-2.jpg" alt="" width="350px"></div>
        </div>
        <div class="data">
            <button class="data-title">驻留数据分析</button>
                <div class="bie"></div>
                <div class="line"></div>
                <p class="pie-title">驻留点总情况</p>
        </div>
        <div class="btn">
            <button class="getLive">获取居住地信息</button>
            <button class="getResi">获取驻留地信息</button>
            <button class="getWork">获取工作地信息</button>
        </div>
    </div>
</template>

<script>
    import Home from '../components/home';
    export default {
        components: {
            Home
        },
        data() {
            return {
                liveLocation: [],
                workLocation: [],
                resiDistrict: [],
                liveDistrict: [],
                workDistrict: [],
                lineChart: null
            }
        },
        watch: {
            resiDistrict() {
                if (this.resiDistrict.length == 157) {
                    var district = this.resiDistrict;
                    var lineOption = this.lineChart.getOption();
                    lineOption.xAxis[0].data = this.arrCheck(district).x;
                    lineOption.series[0].data = this.arrCheck(district).y;
                    lineOption.title[0].text = '驻留地地在各市区的分布情况';
                    this.lineChart.setOption(lineOption, true);
                    this.lineChart.hideLoading();
                }

            },
            liveDistrict() {
                if (this.liveDistrict.length == 54) {
                    var district = this.liveDistrict;
                    var lineOption = this.lineChart.getOption();
                    lineOption.xAxis[0].data = this.arrCheck(district).x;
                    lineOption.series[0].data = this.arrCheck(district).y;
                    lineOption.title[0].text = '居住地在各市区的分布情况';
                    console.log(lineOption.title)
                    this.lineChart.setOption(lineOption, true);
                    this.lineChart.hideLoading();
                }

            },
            workDistrict() {
                if (this.workDistrict.length == 122) {
                    var district = this.workDistrict;
                    var lineOption = this.lineChart.getOption();
                    lineOption.xAxis[0].data = this.arrCheck(district).x;
                    lineOption.series[0].data = this.arrCheck(district).y;
                    lineOption.title[0].text = '工作地在各市区的分布情况';
                    this.lineChart.setOption(lineOption, true);
                    this.lineChart.hideLoading();
                }

            },

        },
        methods: {
            //批量逆地址解析
            async getAllAddress(add, msg) {
                console.log(msg)
                var colorV = 1000;
                if (msg == '工作地') {
                    colorV = 100;
                }
                if (msg == '居住地') {
                    colorV = 500;
                }
                console.log(add)
                var adds = [];
                var that = this;
                var plocation = [];
                var district = [];
                var myGeo = new BMap.Geocoder();
                for (let i = 0; i < add.length; i++) {
                    adds.push(new BMap.Point(add[i][0], add[i][1]));
                }
                for (let i = 0; i < adds.length; i++) {
                    myGeo.getLocation(adds[i], function(rs) {
                        var addComp = rs.addressComponents;
                        plocation.push({
                            msg: msg,
                            value: add[i].concat(colorV),
                            sLocation: '商圈位置(' + rs.business + ')',
                            jLocation: '区位置(' + addComp.district + addComp.street + addComp.streetNumber + ')'
                        });
                        district.push(addComp.district);
                    });
                }
                if (msg == '驻留地') {
                    this.resiDistrict = district;
                }
                if (msg == '居住地') {
                    this.liveDistrict = district;
                }
                if (msg == '工作地') {
                    this.workDistrict = district;
                }
                return plocation;
            },
            arrCheck(arr) {
                var array = arr.concat();
                var newArrX = [];
                var newArrY = [];
                for (var i = 0; i < array.length; i++) {
                    var temp = array[i];
                    var count = 0;
                    for (var j = 0; j < array.length; j++) {
                        if (array[j] == temp) {
                            count++;
                            array[j] = -1;
                        }
                    }
                    if (temp != -1) {
                        newArrX.push(temp);
                        newArrY.push(count);
                    }
                }
                var newArr = {
                    x: newArrX,
                    y: newArrY
                }
                return newArr;
            }
        },
        mounted() {
            var that = this;
            var geoCoordMap = {};
            var data = [];
            var lineOption = {
                title: {
                    text: '驻留地在各市区的分布情况',
                    top: '0',
                    left: 'center',
                    textStyle: {
                        color: "#fff",
                        fontSize: 13
                    }
                },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "shadow",
                        textStyle: {
                            color: "#fff"
                        }

                    },
                },
                calculable: true,
                xAxis: [{
                    type: "category",
                    axisLine: {
                        lineStyle: {
                            color: "rgba(204,187,225,0.5)",
                        }
                    },
                    splitLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    data: [],
                }],

                yAxis: [{
                    type: "value",
                    splitLine: {
                        show: false
                    },
                    axisLine: {
                        lineStyle: {
                            color: "rgba(204,187,225,0.5)",
                        }
                    },

                }],
                dataZoom: [{
                    show: true,
                    height: 20,
                    xAxisIndex: [0],
                    bottom: 10,
                    "start": 10,
                    "end": 80,
                    handleSize: '110%',
                    handleStyle: {
                        color: "#5B3AAE",
                    },
                    textStyle: {
                        color: "rgba(204,187,225,0.5)",
                    },
                    fillerColor: "rgba(67,55,160,0.4)",
                    borderColor: "rgba(204,187,225,0.5)",

                }, {
                    type: "inside",
                    show: true,
                    height: 15,
                    start: 1,
                    end: 35
                }],
                series: [{
                    name: "数量",
                    type: "line",
                    symbolSize: 10,
                    symbol: 'circle',
                    itemStyle: {
                        color: "#6f7de3",
                    },
                    markPoint: {
                        label: {
                            normal: {
                                textStyle: {
                                    color: '#fff'
                                }
                            }
                        },
                        data: [{
                            type: 'max',
                            name: '最大值',

                        }, {
                            type: 'min',
                            name: '最小值'
                        }]
                    },
                    data: [],
                }, ]
            }
            var bieOption = {
                color: ["#EAEA26", "#906BF9", "#FE5656"],
                tooltip: {
                    trigger: 'item',
                    formatter: "{b} : {c} 个 ({d}%)"
                },
                legend: {
                    type: "scroll",
                    orient: "vartical",
                    bottom: 0,
                    left: 0,
                    right: "15",
                    itemWidth: 20,
                    itemHeight: 10,
                    itemGap: 16,
                    textStyle: {
                        color: '#A3E2F4',
                        fontSize: 10,
                        fontWeight: 0
                    },
                    data: ['工作地', '休息地', '即是工作地又是休息地']
                },
                polar: {},
                angleAxis: {
                    interval: 1,
                    type: 'category',
                    data: [],
                    z: 10,
                    axisLine: {
                        show: false,
                        lineStyle: {
                            color: "#0B4A6B",
                            width: 1,
                            type: "solid"
                        },
                    },
                },
                radiusAxis: {
                    min: 40,
                    max: 120,
                    interval: 20,
                    axisLine: {
                        show: false,
                        lineStyle: {
                            color: "#0B3E5E",
                            width: 1,
                            type: "solid"
                        },
                    },
                    axisLabel: {
                        formatter: '{value} %',
                        show: false,
                        padding: [0, 0, 20, 0],
                        color: "#0B3E5E",
                        fontSize: 16
                    },
                    splitLine: {
                        lineStyle: {
                            color: "#0B3E5E",
                            width: 2,
                            type: "solid"
                        }
                    }
                },
                calculable: true,
                series: [{
                    type: 'pie',
                    radius: ["70%", "95%"],
                    hoverAnimation: false,
                    labelLine: {
                        normal: {
                            show: false,
                            length: 10,
                            length2: 55
                        },
                        emphasis: {
                            show: false
                        }
                    },
                    tooltip: {
                        show: false
                    },
                    name: "",
                    data: [{
                        name: '',
                        value: 0,
                        itemStyle: {
                            normal: {
                                color: "#0B4A6B"
                            }
                        }
                    }]
                }, {
                    type: 'pie',
                    radius: ['20%', '80%'],
                    roseType: 'area',
                    zlevel: 10,
                    label: {
                        normal: {
                            show: true,
                            formatter: "{c}",
                            textStyle: {
                                fontSize: 12,
                            },
                            position: 'outside'
                        },
                        emphasis: {
                            show: true
                        }
                    },
                    labelLine: {
                        normal: {
                            show: false,
                            length: 20,
                            length2: 55
                        },
                        emphasis: {
                            show: false
                        }
                    },
                    data: [{
                        value: 103,
                        name: '工作地'
                    }, {
                        value: 35,
                        name: '休息地'
                    }, {
                        value: 19,
                        name: '即是工作地又是休息地'
                    }]
                }, ]
            }
            var option = {
                title: {
                    padding: 20,
                    x: 'center',
                    textStyle: {
                        color: '#fff'
                    },
                    subtextStyle: {
                        fontStyle: 'italic'
                    }
                },
                tooltip: {
                    textStyle: {
                        align: 'left'
                    },
                    trigger: 'item',
                    formatter: function(params) {
                        return params.marker + params.data.msg + '</br>' + params.data.jLocation + '</br>' + params.data.sLocation;
                    }
                },

                grid: {
                    right: '2%',
                    top: '20%',
                    bottom: '15%',
                    width: '15%'
                },

                xAxis: {
                    type: 'value',
                    scale: true,
                    position: 'top',
                    boundaryGap: false,
                    splitNumber: 4,
                    splitLine: {
                        show: false
                    },
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        margin: 2,
                        textStyle: {
                            color: '#aaa'
                        }
                    },
                },

                yAxis: {
                    type: 'category',
                    nameGap: 95,
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        interval: 0,
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    data: []
                },

                visualMap: {
                    min: 0,
                    max: 1000,
                    show: false,
                    inRange: {
                        color: ['tomato', 'green', 'orange']
                    },
                },
                bmap: {
                    center: [123.42, 41.82],
                    zoom: 13,
                    roam: true
                },
                series: [{
                    zlevel: 9999,
                    type: 'scatter',
                    coordinateSystem: 'bmap',
                    data: null,
                    symbolSize: 8
                }]
            };
            $('.nav-top p').html('驻留分析');
            var bieChart = echarts.init(document.querySelector('.bie'));
            bieChart.setOption(bieOption);
            this.lineChart = echarts.init(document.querySelector('.line'));
            var myChart = echarts.init(document.querySelector('#chart'));
            myChart.showLoading();
            this.lineChart.setOption(lineOption);
            $.ajax({
                url: 'http://116.62.79.166:8003/traffic/resident',
                type: 'get',
                success: function(res) {
                    var adds = [];
                    data.length = 0;
                    geoCoordMap = {};
                    for (let i = 0; i < res.length; i++) {
                        geoCoordMap['驻留地' + (i + 1)] = res[i]['驻留地' + (i + 1)];
                    }
                    for (var key in geoCoordMap) {
                        adds.push(geoCoordMap[key]);
                    }
                    that.getAllAddress(adds, '驻留地').then(function(value) {
                        setTimeout(() => {
                            option.series[0].data = value;
                            myChart.setOption(option);
                            myChart.hideLoading();
                        }, 2500)
                    });
                }
            })

            //获取驻留地信息
            $('.getResi').on('click', function() {
                myChart.showLoading();
                $.ajax({
                    url: 'http://116.62.79.166:8003/traffic/resident',
                    type: 'get',
                    success: function(res) {
                        var adds = [];
                        data.length = 0;
                        geoCoordMap = {};
                        for (let i = 0; i < res.length; i++) {
                            geoCoordMap['驻留地' + (i + 1)] = res[i]['驻留地' + (i + 1)];
                        }
                        for (var key in geoCoordMap) {
                            adds.push(geoCoordMap[key]);
                        }
                        that.getAllAddress(adds, '驻留地').then(function(value) {
                            setTimeout(() => {
                                option.series[0].data = value;
                                myChart.setOption(option);
                                myChart.hideLoading();
                            }, 2500)
                        });
                    }
                })
            });
            // 获取工作地信息
            $('.getWork').on('click', function() {
                myChart.showLoading();
                $.ajax({
                    url: 'http://116.62.79.166:8003/traffic/work',
                    type: 'get',
                    success: function(res) {
                        var adds = [];
                        data.length = 0;
                        geoCoordMap = {};
                        for (let i = 0; i < res.length; i++) {
                            geoCoordMap['工作地' + (i + 1)] = res[i]['工作地' + (i + 1)];
                        }
                        for (var key in geoCoordMap) {
                            adds.push(geoCoordMap[key]);
                        }
                        that.getAllAddress(adds, '工作地').then(function(value) {
                            setTimeout(() => {
                                console.log(that.workDistrict)
                                option.series[0].data = value;
                                myChart.setOption(option);
                                myChart.hideLoading();
                            }, 2500)
                        });
                    }
                })
            });
            //获取居住地信息
            $('.getLive').on('click', function() {
                myChart.showLoading();
                $.ajax({
                    url: 'http://116.62.79.166:8003/traffic/live',
                    type: 'get',
                    success: function(res) {
                        var adds = [];
                        data.length = 0;
                        geoCoordMap = {};
                        for (let i = 0; i < res.length; i++) {
                            geoCoordMap['居住地' + (i + 1)] = res[i]['居住地' + (i + 1)];
                        }
                        for (var key in geoCoordMap) {
                            adds.push(geoCoordMap[key]);
                        }
                        that.getAllAddress(adds, '居住地').then(function(value) {
                            setTimeout(() => {
                                console.log(that.liveDistrict)
                                option.series[0].data = value;
                                myChart.setOption(option);
                                myChart.hideLoading();
                            }, 2500)
                        });
                    }
                })
            });

        }
    }
</script>

<style scoped>
    .jzdfx {
        position: fixed;
        height: 100%;
        width: 100%;
        background: #051b4a;
        background: url('../assets/bg.png');
        background-size: 100% 100%;
    }
    
    .chart-container {
        position: fixed;
        width: 60%;
        left: 19.5%;
        height: 76%;
        top: 12%;
        z-index: -1;
        border-radius: 1px;
        border: 2px solid rgb(66, 100, 255);
    }
    
    #chart {
        position: absolute;
        height: 100%;
        width: 100%;
    }
    
    .chart-container img {
        vertical-align: middle;
    }
    
    .top-left {
        position: absolute;
        left: -3px;
        top: -6px !important;
        transform: rotateX(180deg);
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .top-right {
        position: absolute;
        right: -3px;
        top: -6px;
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .bottom-right {
        position: absolute;
        right: -3px;
        bottom: -7px;
        transform: rotateX(180deg);
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .bottom-left {
        position: absolute;
        left: -3px;
        bottom: -7px;
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .data {
        position: fixed;
        height: 80%;
        width: 18%;
        right: 0px;
        background: url('../assets/data.png');
        background-size: 100% 100%;
        top: 10%;
        z-index: -1;
    }
    
    .data-title {
        background: none;
        border: none;
        background-image: url(../assets/data-bg.png);
        background-size: 100% 100%;
        width: 230px;
        height: 40px;
        padding-bottom: 7px;
        position: relative;
        color: white;
        outline: none;
        color: turquoise;
        font-weight: 1000;
        opacity: 0.9;
        top: 20px;
    }
    
    .btn {
        position: fixed;
        left: 50%;
        height: 50px;
        bottom: 10px;
        width: 580px;
        transform: translateX(-50%);
        margin-left: 20px;
        width: 100%;
    }
    
    .btn button {
        color: white;
        font-weight: 1000;
        font-size: 13px;
        outline: none;
        background-image: linear-gradient(to left, #355288, #0f378f);
        border-radius: 10px;
        border: rgba(127, 255, 212, 0.719) 1.5px solid;
        box-shadow: 0 0 1px 1px #313ac7;
        width: 180px;
        height: 45px;
        margin-right: 20px;
    }
    
    .btn button:hover {
        color: rgba(127, 255, 212, 0.719);
    }
    
    .bie {
        position: absolute;
        width: 200px;
        height: 200px;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-102%);
    }
    
    .line {
        position: absolute;
        width: 100%;
        height: 250px;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(2%);
    }
    
    .pie-title {
        position: absolute;
        font-size: 13px;
        color: white;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-234px);
        font-weight: 1000;
    }
</style>


