import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import Rkmd from '@/pages/rkmd'
import Jtyd from '@/pages/jtyd'
import Rkcx from '@/pages/rkcx'
import Zl from '@/pages/zl'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'Index',
        component: Index
    }, {
        path: '/rkmd',
        name: 'Rkmd',
        component: Rkmd
    }, {
        path: '/jtyd',
        name: 'Jtyd',
        component: Jtyd
    }, {
        path: '/rkcx',
        name: 'Rkcx',
        component: Rkcx
    }, {
        path: '/zl',
        name: 'Zl',
        component: Zl
    }, ]
})


