<template>
    <div class="jtyd">
        <div class="average" style="display:none">
            <div class="line animated fadeIn"></div>
            <div class="back el-icon-close animated fadeIn" @click="closeAver"></div>
        </div>
        <Home></Home>
        <div class="chart-container">
            <div id="chart"></div>
            <div class="top-left"><img src="@/assets/enter-border-1.jpg" alt="" width="350px"></div>
            <div class="bottom-left"><img src="@/assets/enter-border-1.jpg" alt="" width="350px"></div>
            <div class="top-right"><img src="@/assets/enter-border-2.jpg" alt="" width="350px"></div>
            <div class="bottom-right"><img src="@/assets/enter-border-2.jpg" alt="" width="350px"></div>
        </div>
        <div class="data">
            <button class="data-title">各站点交通拥堵状况</button>
            <div class="datalist-title">
                <p style="color:orange">时间段</p>
                <p style="color:tomato">站点状况</p>
                <p style="color:#6cdf18">标识码</p>
            </div>
            <div class="data-content">
                <el-collapse v-model="activeName" accordion>
                    <el-collapse-item :title="item.name"  v-for="item in stationInfo" :key="item.name">
                          <div class="datalist"  @click="averSpeed(item)"><p style="color:orange;font-weight:1000;font-size:15px;cursor:pointer">展示该站点一天的平均速度</p></div>
                        <hr style="margin:0;padding:0">
                        <div class="datalist" v-for="(condition,index) in item.condition" :key="index">
                            <p>{{(index>=10)?index:'0'+index}}-{{(index+1)>=10?index+1:'0'+ parseInt(index+1)}}</p>
                            <p>{{condition==null?'畅通':condition}}</p>
                            <p v-if="condition=='畅通'" class="tc"></p>
                            <p v-if="condition=='拥挤'" class="yj"></p>
                            <p v-if="condition=='堵塞'" class="ds"></p>
                            <p v-if="condition==null" class="tc"></p>
                        </div>
                    </el-collapse-item>
                </el-collapse>
            </div>
        </div>
        <div class="btn">
            <button class="next">获取下一时间段数据</button>
            <button class="time">{{time}}:00 - {{time+1}}:00</button>
            <button class="prev">获取上一时间段数据</button>
        </div>
    </div>
</template>

<script>
    import Home from '../components/home';
    export default {
        components: {
            Home
        },
        methods: {
            closeAver(){
                $('.average').css('display', 'none');
            },
            averSpeed(item) {
                $('.average').css('display', 'block');
                this.setLineOption(item);
            },
            setLineOption(item) {
                var that =this;
                var option = {
                    title: {
                        text: '折线图',
                        textStyle: {
                            align: 'center',
                            color: '#fff',
                            fontSize: 20,
                        },
                        top: '5%',
                        left: 'center',
                    },
                    grid: {
                        top: '15%',
                        left: '5%',
                        right: '5%',
                        bottom: '15%',
                        // containLabel: true
                    },
                    tooltip:{
                        axisPointer: {
            lineStyle: {
                color: {
                    type: 'linear',
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [{
                        offset: 0,
                        color: 'rgba(0, 255, 233,0)'
                    }, {
                        offset: 0.5,
                        color: 'rgba(255, 255, 255,1)',
                    }, {
                        offset: 1,
                        color: 'rgba(0, 255, 233,0)'
                    }],
                    global: false
                }
            },
        },
                        trigger:'axis',
                        
                    textStyle: {
                        align: 'left'
                    },
                    },
                    xAxis: [{
                        type: 'category',
                        axisLine: {
                            show: true
                        },
                        splitArea: {
                            // show: true,
                            color: '#f00',
                            lineStyle: {
                                color: '#f00'
                            },
                        },
                        axisLabel: {
                            color: '#fff'
                        },
                        splitLine: {
                            show: false
                        },
                        boundaryGap: false,
                        data: ['0时','1时','2时','3时','4时','5时','6时','7时','8时','9时','10时','11时','12时','13时','14时','15时','16时','17时','18时','19时','20时','21时','22时','23时'],

                    }],

                    yAxis: [{
                        type: 'value',
                        min: 0,
                        // max: 140,
                        splitNumber: 4,
                        splitLine: {
                            show: true,
                            lineStyle: {
                                color: 'rgba(255,255,255,0.1)'
                            }
                        },
                        axisLine: {
                            show: false,
                        },
                        axisLabel: {
                            show: false,
                            margin: 20,
                            textStyle: {
                                color: '#d1e6eb',

                            },
                        },
                        axisTick: {
                            show: false,
                        },
                    }],
                    series: [{
                        name: '平均速度（m/s）',
                        type: 'line',
                        showAllSymbol: true,
                        symbol: 'circle',
                        symbolSize: 25,
                        lineStyle: {
                normal: {
                    color: "#00ca95",
                    shadowColor: 'rgba(0, 0, 0, .3)',
                    shadowBlur: 0,
                    shadowOffsetY: 5,
                    shadowOffsetX: 5,
                },
            },
            label: {
                show: true,
                position: 'top',
                textStyle: {
                    color: '#00ca95',
                }
            },

            itemStyle: {
                color: "#00ca95",
                borderColor: "#fff",
                borderWidth: 3,
                shadowColor: 'rgba(0, 0, 0, .3)',
                shadowBlur: 0,
                shadowOffsetY: 2,
                shadowOffsetX: 2,
            },
            tooltip: {
                show: false
            },
            areaStyle: {
                normal: {
                    color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                            offset: 0,
                            color: 'rgba(0,202,149,0.3)'
                        },
                        {
                            offset: 1,
                            color: 'rgba(0,202,149,0)'
                        }
                    ], false),
                    shadowColor: 'rgba(0,202,149, 0.9)',
                    shadowBlur: 20
                }
            },
                        data: []
                    }]
                };
                this.lineChart = echarts.init(document.querySelector('.line'));
                $.ajax({
                    url:'http://116.62.79.166:8003/traffic/speed?name=' + item.name,
                    type:'get',
                    success:function(res){
                        console.log(res)
                        option.series[0].data = res;
                        option.title.text = item.name + ' 24小时站点平均速度(m/s)';
                        that.lineChart.setOption(option);
                    }
                })
            }
        },
        data() {
            return {
                time: 0,
                activeName: 1,
                stationInfo: [],
                lineChart: null
            }
        },
        created() {
            var that = this;
            $.ajax({
                url: "http://116.62.79.166:8003/traffic/station",
                type: "get",
                success: function(data) {
                    that.stationInfo = data;
                }
            })
        },
        mounted() {
            //兼容
            var dataHeight = $('.data').height();
            var marginH = $('.datalist-title').height();
            $('.data-content').height(dataHeight - 75 - marginH);
            $('.data-content').css('top', marginH + 67 + 'px');
            $('.nav-top p').html('交通拥堵分析');
            var that = this;
            let myChart = echarts.init(document.getElementById('chart'));
            myChart.showLoading();
            var uploadedDataURL = "https://geo.datav.aliyun.com/areas_v2/bound/210100_full.json";
            //热力点的名字及经纬度
            var geoCoordMap = {};
            //热力点的名字及其值
            var data = [];
            //热力图格式处理函数
            var convertData = function(data) {
                var res = [];
                for (var i = 0; i < data.length; i++) {
                    var geoCoord = geoCoordMap[data[i].name];
                    if (geoCoord) {
                        res.push({
                            name: data[i].name,
                            value: geoCoord.concat(data[i].value),
                            remark: data[i].remark
                        });
                    }
                }
                return res;
            };
            //地图基础设置
            var option = {
                bmap: {
                    center: [123.42, 41.82],
                    zoom: 13,
                    roam: true
                },
                tooltip: {
                    textStyle: {
                        align: 'left'
                    },
                    trigger: 'item',
                    formatter: function(params) {
                        return params.marker + params.data.remark;
                    }
                },
                visualMap: {
                    top: 'top',
                    left: '10',
                    min: 0,
                    max: 300,
                    seriesIndex: 0,
                    calculable: false,
                    inRange: {
                        color: ['#26eb45', 'yellow', 'red']
                    }
                },
                series: [{
                    type: 'scatter',
                    coordinateSystem: 'bmap',
                    data: convertData(data),
                    pointSize: 4,
                    blurSize: 0
                }]
            };
            //获取数据
            var promise = new Promise(function(suc, fail) {
                    $.ajax({
                        url: 'http://116.62.79.166:8003/traffic/jam?time=0',
                        type: 'get',
                        dataType: 'json',
                        success: function(res) {
                                //对接数据
                            $(res).each(function(i, item) {
                                if (i == 0) {
                                    // that.time = 0;
                                } else {
                                    var value = 100;
                                    if (res[i].state == '畅通') {
                                        value = 100;
                                    }
                                    if (res[i].state == '拥挤') {
                                        value = 200;
                                    }
                                    if (res[i].state == '堵塞') {
                                        value = 300;
                                    }
                                    geoCoordMap['站点' + i] = res[i]['站点' + i];
                                    data.push({
                                        name: '站点' + i,
                                        value: value,
                                        remark: res[i].name
                                    })
                                }
                            });
                            suc();
                        }
                    })
                })
                //构建地图
            promise.then(function() {
                $.getJSON('http://116.62.79.166:8003/traffic/jam?time=0', function() {
                    option.series[0].data = convertData(data);
                    myChart.setOption(option);
                    myChart.hideLoading();
                });
            });
            $('.prev').on('click', function() {
                myChart.showLoading();
                if (that.time == 0) {
                    that.time = 23;
                } else {
                    that.time = that.time - 1;
                }
                console.log(that.time)
                $.ajax({
                    url: "http://116.62.79.166:8003/traffic/jam?time=" + that.time,
                    type: "get",
                    dataType: "json",
                    success: function(res) {
                        data.length = 0;
                        geoCoordMap = {};
                        $(res).each(function(i, item) {
                            if (i == 0) {
                                // that.time = 0;
                            } else {
                                var value = 100;
                                if (res[i].state == '畅通') {
                                    value = 100;
                                }
                                if (res[i].state == '拥挤') {
                                    value = 200;
                                }
                                if (res[i].state == '堵塞') {
                                    value = 300;
                                }
                                geoCoordMap['站点' + i] = res[i]['站点' + i];
                                data.push({
                                    name: '站点' + i,
                                    value: value,
                                    remark: res[i].name

                                })
                            }
                        });
                        option.series[0].data = convertData(data);
                        myChart.setOption(option);
                        myChart.hideLoading();
                    }
                });
            });
            $('.next').on('click', function() {
                myChart.showLoading();
                if (that.time == 23) {
                    that.time = 0;
                } else {
                    that.time = that.time + 1;
                }
                console.log(that.time)
                $.ajax({
                    url: "http://116.62.79.166:8003/traffic/jam?time=" + that.time,
                    type: "get",
                    dataType: "json",
                    success: function(res) {
                        data.length = 0;
                        geoCoordMap = {};
                        $(res).each(function(i, item) {
                            if (i == 0) {
                                // that.time = 0;
                            } else {
                                var value = 100;
                                if (res[i].state == '畅通') {
                                    value = 100;
                                }
                                if (res[i].state == '拥挤') {
                                    value = 200;
                                }
                                if (res[i].state == '堵塞') {
                                    value = 300;
                                }
                                geoCoordMap['站点' + i] = res[i]['站点' + i];
                                data.push({
                                    name: '站点' + i,
                                    value: value,
                                    remark: res[i].name
                                })
                            }
                        });
                        option.series[0].data = convertData(data);
                        myChart.setOption(option);
                        myChart.hideLoading();
                    }
                });
            });
            myChart.on('click',function(params){
                var item ={
                    name: params.data.remark
                };
                that.averSpeed(item);
            })
        },

    }
</script>

<style scoped>
    .chart-container {
        position: fixed;
        width: 60%;
        left: 19.5%;
        height: 76%;
        top: 12%;
        z-index: -1;
        border-radius: 1px;
        border: 2px solid rgb(59, 73, 134);
    }
    
    #chart {
        position: absolute;
        height: 100%;
        width: 100%;
    }
    
    .jtyd {
        position: fixed;
        height: 100%;
        width: 100%;
        background: #051b4a;
        background: url('../assets/bg.png');
        background-size: 100% 100%;
    }
    
    .data {
        position: fixed;
        height: 80%;
        width: 18%;
        right: 0px;
        background: url('../assets/data.png');
        background-size: 100% 100%;
        top: 10%;
        z-index: -1;
    }
    
    .data-title {
        background: none;
        border: none;
        background-image: url(../assets/data-bg.png);
        background-size: 100% 100%;
        width: 230px;
        height: 40px;
        padding-bottom: 7px;
        position: relative;
        color: white;
        outline: none;
        color: turquoise;
        font-weight: 1000;
        opacity: 0.9;
        top: 20px;
    }
    
    .btn {
        position: fixed;
        left: 50%;
        height: 50px;
        bottom: 0;
        width: 580px;
        transform: translateX(-50%);
    }
    
    .btn button {
        color: white;
        font-weight: 1000;
        font-size: 13px;
        outline: none;
        background-image: linear-gradient(to left, #355288, #0f378f);
        border-radius: 10px;
        border: rgba(127, 255, 212, 0.719) 1.5px solid;
        box-shadow: 0 0 1px 1px #313ac7;
        width: 180px;
        height: 45px;
    }
    
    .prev {
        position: absolute;
        left: 0%;
        bottom: 10px;
        outline: none;
        color: white;
        border-radius: 15rem;
    }
    
    .next {
        position: absolute;
        left: 400px;
        bottom: 10px;
        outline: none;
        color: white;
        border-radius: 15rem;
    }
    
    .btn button:hover {
        color: rgba(127, 255, 212, 0.719);
    }
    
    .data-content {
        position: absolute;
        width: 100%;
        top: 67px;
        overflow: scroll;
        scrollbar-width: none;
        /* Firefox */
        -ms-overflow-style: none;
        /* IE 10+ */
    }
    
    /deep/ .el-collapse-item__header {
        width: 85%;
        margin-left: 5%;
        border-radius: 10px;
        border: 1.5px solid rgba(127, 255, 212, 0.377) !important;
        box-shadow: none;
        margin-top: 5px;
        background: linear-gradient(to left, #355288, #0f378f);
        color: white;
        padding-left: 5%;
        font-weight: 1000;
        font-size: 13px;
    }
    
    /deep/ .el-collapse {
        border: none;
    }
    
    /deep/ .el-collapse-item__wrap {
        background: linear-gradient(to left, #355288, #0f378f);
        border: none;
        width: 80%;
        margin-left: 10%;
        border-left: 1.5px solid rgba(127, 255, 212, 0.377);
        border-right: 1.5px solid rgba(127, 255, 212, 0.377);
        border-bottom: 1.5px solid rgba(127, 255, 212, 0.377);
    }
    
    /deep/ .el-collapse-item__content {
        color: white;
        width: 90%;
        margin-left: 5%;
        padding-bottom: 0;
    }
    
    .datalist {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
    }
    
    .datalist-title {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 90%;
        margin-left: 5%;
        color: white;
        border: 1.5px solid rgba(127, 255, 212, 0.377);
        background: linear-gradient(to left, #355288, #0f378f);
        margin-top: 25px;
        align-items: center;
    }
    
    .datalist-title p {
        margin: 10px;
        font-weight: 1000;
        font-size: 14px;
    }
    
     ::-webkit-scrollbar {
        display: none;
    }
    
    .tc {
        height: 12px;
        width: 12px;
        background: greenyellow;
        border-radius: 15rem;
    }
    
    .yj {
        height: 12px;
        width: 12px;
        background: orange;
        border-radius: 15rem;
    }
    
    .ds {
        height: 12px;
        width: 12px;
        background: tomato;
        border-radius: 15rem;
    }
    
    .chart-container img {
        vertical-align: middle;
    }
    
    .top-left {
        position: absolute;
        left: -3px;
        top: -7px;
        transform: rotateX(180deg);
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .top-right {
        position: absolute;
        right: -3px;
        top: -7px;
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .bottom-right {
        position: absolute;
        right: -3px;
        bottom: -7px;
        transform: rotateX(180deg);
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .bottom-left {
        position: absolute;
        left: -3px;
        bottom: -7px;
        filter: hue-rotate(57deg);
        pointer-events: none;
    }
    
    .time {
        position: absolute;
        bottom: 10px;
        left: 200px;
    }
    
    .time:hover {
        color: white !important;
    }
    
    .average {
        position: fixed;
        height: 100%;
        width: 100%;
        background: rgba(0, 0, 0, 0.445);
        z-index: 999;
    }
    
    .line {
        position: absolute;
        height: 70%;
        width: 70%;
        top: 15%;
        left: 15%;
        background:url('../assets/data3.png');
        background-size: 100% 100%;
    }
    .back{
        position: absolute;
        right: 16%;
        top: 17%;
        font-size: 40px;
        color:white;
        cursor: pointer;
    }
</style>


